import UserPhoto from "../../sass/styled-components/UserPhoto";
import InputBox from "../../Views/InputBox/InputBox";
import "./AddComment.scss";
export interface CommentType {
  commentContent?: string;
  commentId?: number;
  nickName: string;
  updatedAt?: string | Date;
  userProfileImage?: string;
  userProfileImageUrl?: string;
}
interface AddCommentTypeProps {
  onSubmit: React.FormEventHandler<HTMLFormElement> | undefined;
  onChangeValue: ((e: React.ChangeEvent<HTMLInputElement>) => void) | undefined;
  getUserProfile?: CommentType | undefined;
}

const AddComment = (props: AddCommentTypeProps) => {
  const { onSubmit, onChangeValue, getUserProfile }: AddCommentTypeProps =
    props;
  return (
    <form className="AddComment" onSubmit={onSubmit}>
      <span className="AddComment__user">
        <UserPhoto
          size="44px"
          userProfileImage={getUserProfile?.userProfileImageUrl}
        />
      </span>
      <span className="AddComment__input">
        <InputBox
          placeholder={`${getUserProfile?.nickName}()  ...`}
          type="text"
          name="commentContent"
          onChangeValue={onChangeValue}
        />
        <button className="AddComment__input--button"></button>
      </span>
    </form>
  );
};
export default AddComment;
